<template>
  <div>
    <van-nav-bar class="v-nav-bar"
                style="border-bottom: 1px solid #f1f1f1;"
                v-if="back"
                :title="title"
                fixed
                left-arrow
                @click-left="_back"
                @click-right="onClickRight"
                :right-text="rightText">
      <template #right>
        <van-icon 
                :name="iconName"
                color="grey"
                size="20" />
      </template>
    </van-nav-bar>
    <van-nav-bar style="border-bottom: 1px solid #f1f1f1;"
                v-else
                fixed
                :title="title" />
    <form v-if="noSearch==false" action="/" class = "search">
        <van-search
                v-if="isSearch"
                v-model="search.word"
                shape="round"
                show-action
                background="#4fc08d"
                placeholder="请输入搜索关键词"
                @search="onSearch"
                @cancel="onCancel" />
        <div v-else style="height:54px;background:#4fc08d;"/>
    </form>
    <div :style="noSearch?'height:47px;':'height:101px;'" />
  </div>
</template>
<script>
import { NavBar, Icon } from "vant";
export default {
  props: {
    back: {
      type:Boolean,
      default:true,
    },
    title: '',
    iconName:'',
    iconTo:'',
    rightText: '',
    backPath: '',
    noSearch:{
      type:Boolean,
      default:false,      
    },
    isSearch:{
      type:Boolean,
      default:true,
    },
    rightClick: {
      type: Function,
      default: () => {
        return;
      }
    }
  },
  data(){
    return{
    search:{
      word:'',
    },
    };
  },
  components: {
    "van-nav-bar": NavBar,
    "van-icon": Icon
  },
  methods: {
    _back () {
      if (this.backPath) {
        return this.$router.push({ path: this.backPath });
      }
      this.$router.go(this.backPath || -1);
    },
    onClickRight () {
      console.log(this.iconTo )
      if(this.iconTo == undefined || this.iconTo == "")
        this.$emit("iconClick");   
      else
        this.$router.push({path:this.iconTo});   
    },
    onSearch(){
      this.$emit("querySearch",this.search);  
    },
    onCancel(){
      this.$emit("cancelSearch");  
    },
  }
};
</script>
<style scoped>
.nav-bar {
  background: grey;
}
.nav-bar >>> * {
  color: white;
}
.search{
  position: fixed;
  top:47px;
  width:100%;
  z-index: 4;
}
</style>



