<template >
  <div class="login-container">
    <vehicle-header 
                noSearch
                :back="false"
                title="查分助手"></vehicle-header>
    <h2>帐号登陆</h2>
    <div class="login-logo">
      <img src="@/assets/logo.png" />
    </div>
    <div v-if="type=='pw'"  class="login-input">
      <van-cell-group inset>    
      <van-field size="large"
                 v-model="userQuery.account"
                 placeholder="请输入帐号"
                 type="text"
                 label="帐号" />

      <van-field size="large"
                 v-model="userQuery.password"
                 placeholder="请输入密码"
                 type="password"
                 label="密码" > 
          <template #button />
        </van-field>      
      </van-cell-group>  
      <br/>
      <p style="text-decoration: underline;color:red;" @click="onTypeSelect('sms')">短信验证码登录</p>         
    </div>
    <div v-else class="login-input">
      <van-cell-group inset>
      <van-field size="large"
                 v-model="userQuery.account"
                 placeholder="请手机号码"
                 type="text"
                 label="手机号" />
        <van-field size="large"
                  v-model="userQuery.smsCode"
                  placeholder="请输短信验证码"
                  type=""
                  label="验证码" > 
          <template #button>
            <van-button size="mini" 
                  native-type="button" 
                  :disabled="smsBtnDisabled" 
                  type="primary" 
                  @click="sendSMSCode">{{smsBtnName}}</van-button>
          </template>
        </van-field>
      </van-cell-group>
      <br/>
      <p style="text-decoration: underline;color:red;" @click="onTypeSelect('pw')">密码登录</p>  
    </div>  
    <div class="login-btn">
      <van-button @click="login()"
                  v-show="!loading"
                  block
                  type="info">登陆</van-button>
      <van-button disabled
                  v-show="loading"
                  block
                  loading
                  type="info"
                  loading-text="正在登陆..." />
    </div>
  </div>
</template>
<script>
import { Field, Divider } from "vant";
import Header from "@/components/Header.vue";
export default {
  watch: {
    '$route' (to, from) {
    }
  },
  components: {
    "vehicle-header": Header,
    "van-field": Field,
    "van-divider": Divider
  },
  data () {
    return {
      codeImgSrc: "",
      type:"pw",
      smsBtnDisabled:false,
      smsBtnName:"发送验证码",
      loading: false,
      userQuery: {
        account: "",
        password: "",
        phone:"",
        smsCode:"",
        token:"",
      },
      userData:null,
    };
  },
  created () {
  },
  methods: {
    onTypeSelect(type){
      this.type = type;
    },
    sendSMSCode(){      
      if(this.userQuery.account.match("^1+[0-9]{10,10}$") === null)
        return this.$toast("请输入正确的手机号");        

      let that = this;
      this.zone.getSMSCode(this.userQuery.account).then(ret =>{
        if(ret.status == 200 && ret.data.status==200){
            that.userQuery.phone = that.userQuery.account;
            that.userQuery.token = ret.data.data.token; 
            that.$toast("短信验证码已发送");  
            that.smsBtnDisabled = true;            
            that.smsSendTime(61,(time)=>{
              if(time>0 && that.userQuery.account == that.userQuery.phone){
                that.smsBtnName="重新发送("+time+")";
              }else{
                that.smsBtnName="发送验证码";
                that.smsBtnDisabled = false;
              }
            })
        }
      });
    },
    login () {
      if(this.type == "pw"){     
        this.userLogin();
      }else{   
        this.phoneLogin();
      }
    },
    userLogin(){
      if (this.userQuery.account == "" || this.userQuery.account.trim() == "")
        return this.$toast("请输入用户名");
      if (this.userQuery.password == "" || this.userQuery.password.trim() == "")
        return this.$toast("请输入密码");

      this.loading = true;

      this.zone.userLogin(this.userQuery.account,this.userQuery.password).then(ret=>{
        this.loading = false;
        this.$toast(ret.data.message);
        if(ret.status==200 && ret.data.status == 200){
          this.$router.push('/');        
        }
      });
    },
    phoneLogin(){
      if(this.userQuery.account.match("^1+[0-9]{10,10}$") === null)
        return this.$toast("请输入正确的手机号");  
      
      if(this.userQuery.smsCode.match("^[0-9]{6,6}$") === null)
        return this.$toast("请输入正确的短信验证码");   

      this.loading = true;

      this.zone.phoneLogin(this.userQuery.account,this.userQuery.smsCode,this.userQuery.token).then(ret=>{
        this.loading = false;
        this.$toast(ret.data.message);
        if(ret.status==200 && ret.data.status == 200){
          this.$router.push('/');        
        }
      });      
    },
    smsSendTime(time,callback){
      time--;
      if(time > 0){
        setTimeout(() => {
            this.smsSendTime(time, callback);
        }, 1000);
      }
      callback(time);
    }
  },
};
</script>
<style lang="less" scoped>

h2 {
  padding-left: 40px;
  padding-top: 1rem;
  margin-bottom: 8px;
  font-size: 21px;
  font-weight: 500;
  text-align: left;
}
.login-container {
  .login-logo {
    //height: 50px;
    text-align: center;
    img {
      height: 160px;
    }
  }
  .login-input {
    padding: 1rem 1.5rem;
    img {
      height: 20px;
      position: relative;
    }
  }
  .login-btn {
    padding: 1rem 1.5rem;
    > button {
      border-radius: 7px;
    }
  }
  .register {
    text-align: right;
    a {
      color: #a7a7a7;
      padding-right: 1rem;
      font-size: 13px;
    }
  }
  .login-account {
    position: relative;
    padding: 0rem 1.7rem;
    color: #afafaf;
    font-size: 13px;
    a:last-child {
      float: right;
    }
  }
  .login-line {
    margin-top: 1rem;
  }
  .login-other {
    display: flex;
    .item {
      img {
        height: 50px;
      }
      flex: 1;
      text-align: center;
      margin-top: 8px;
      padding: 0 4px;
      color: #646566;
      font-size: 12px;
    }
  }
}
</style>



